.profile{
    display: flex;
}

.profileRight{
    flex:9;
}
.profileCover{
    height: 320px;
    position: relative;
}
.profileCoverImg{
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.profileUserImg{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    margin: auto;
    left:0;
    right: 0;
    top:150px;
    border:3px solid white;


}

.profileInfo{
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
}
.profileRightBottom{
    display: flex;

}

.profileInfoName{
    font-size: 24px;
}
.profileInfoDesc{
    font-weight: 300;
}